html {
  background-color:teal;
}
#root {
  width:95%;
  height:75vh;
  margin:50px auto;
  border:3px solid white;
  overflow:hidden;
  border-radius:15px;
}
.container {
  display:inline-block;
  width:50%;
  height:75vh;
  box-sizing:border-box;
  padding:4.5%;
  background-color:white;
  font-family:'Open Sans';
  font-size:1.02em;
}
#goalnodes_container {
   border-right:5px dashed gray;
   background-color:#fafafa;
   overflow:auto;
}
#model_container {
   overflow:hidden;
   padding-top:2%;
}
.gnInput {
   display:inline-flex;
   align-items:center;
   justify-content:center;
   margin-left:5px;
   margin-right:5px;
   vertical-align:0px;
   min-width:50px;
   max-width:15%;
   min-height:5vh;
   max-height:5vh;
   border:none;
   border-radius:5px;
   box-shadow:0 0 3px #aaa;
   text-align:center;
   font-size:1em;
   font-family:'Open Sans';
   outline:none;
   box-sizing:border-box;
   color:black;
}
.gnSelect, .nextButton {
   min-height:5vh;
   max-height:5vh;
   min-width:80px;
   max-width:25%;
   border:none;
   outline:none;
   border-radius:5px;
   font-size:1em;
   font-family:'Open Sans';
   box-shadow:0 0 3px #aaa;
   margin-left:5px;
   margin-right:5px;
}
.gnode {
   margin-top:55px;
   line-height:3em;
}
.vertSVG {
   border:3px dashed darkgray;
}
.nextButton {
   position:fixed;
   bottom:10px;
   right:10px;
   font-variant:small-caps;
   cursor:pointer;
}
.gnSelect {
   text-align-last:center;
}
